<template>
  <div id="success">
    <div class="header">
      <span class='iconfont icon-back' @click=back()></span>
      
    </div>
    <div class="body">
      <span class='iconfont icon-icon-'></span>
      <h4></h4>
      <p>24</p>
      <div @click='success()'></div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "success",
    methods: {
      back() {
        this.$router.go(-1)
      },
      success() {
        this.$router.go(-2)
      }
    }
  }
</script>

<style scoped lang='less'>
  @import "success";
</style>



