import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home'
import newcar from '@/components/newcar/newcar'
import newdetail from '@/components/newcar/detail'
import useddetail from '@/components/usedcar/detail'
import usedcar from '@/components/usedcar/usedcar'
import newSearch from '@/components/search/newSearch'
import usedSearch from '@/components/search/usedSearch'
import appointment from '@/components/appointment/appointment'
import aboutus from '@/components/aboutus/aboutus'
import ndemo from '@/components/newcar/demo'
import login from "@/components/login/login"
import store from "@/components/store/store"
import success from "@/components/login/success"

Vue.use(Router)

export default new Router({
  // mode: 'history',
  mode: 'hash',
  base: '/wx/',
  routes: [
    {
      path: '', name: 'index', component: home, meta: {title: ''}
    },
    {path: '/home', name: 'home', component: home, meta: {title: ''}},
    {path: '/newcar', name: 'newcar', component: newcar, meta: {title: ''}},
    {path: '/newcar/detail/:id', name: 'newdetail', component: newdetail, meta: {title: ''}},
    {path: '/usedcar/detail/:id', name: 'useddetail', component: useddetail, meta: {title: ''}},
    {path: '/usedcar', name: 'usedcar', component: usedcar, meta: {title: ''}},
    {path: '/search/new', name: 'newSearch', component: newSearch, meta: {title: ''}},
    {path: '/search/used', name: 'usedSearch', component: usedSearch, meta: {title: ''}},
    {path: '/appointment', name: 'appointment', component: appointment, meta: {title: ''}},
    {path: '/aboutus', name: 'aboutus', component: aboutus, meta: {title: ''}},
    {path: '/login/:id', name: 'login', component: login, meta: {title: ''}},
    {path: '/store', name: 'store', component: store, meta: {title: ''}},
    {path: '/success', name: 'success', component: success, meta: {title: ''}},
    {path: '/ndemo', name: 'ndemo', component: ndemo, meta: {title: ''}},
  ],

})



