<template>
  <div id="detail">
    <div class="header">
      <h1 class="logo">
      </h1>
      <div class='title'>
        
      </div>
      <div class="option">
        <span label="undocked drawer" @click="toggle(true)" class='iconfont icon-bars'></span>
        <mu-drawer right :open="open" :docked="docked" @close="toggle()">
          <ul @itemClick="docked ? '' : toggle()">
            <li>
              <router-link :to="'/home'"></router-link>
            </li>
            <li>
              <router-link :to="{path:'/newcar',query:{isNewCar:true,size:10,page:1,orderType:1}}"></router-link>
            </li>
            <li>
              <router-link :to="{path:'/usedcar',query:{isNewCar:false,size:10,page:1,orderType:1}}"></router-link>
            </li>
            <div class="bb"></div>
            <!--<li>-->
            <!--<router-link :to="'/demo'"></router-link>-->
            <!--</li>-->
            <li>
              <router-link :to="'/appointment'"></router-link>
            </li>
            <li>
              <router-link :to="'/aboutus'"></router-link>
            </li>
            <div class="bb"></div>
          </ul>
        </mu-drawer>
      </div>
    </div>
    <mt-swipe :auto="3000">
      <mt-swipe-item v-for="(item,index) in carDetail.imgList"><img :src="item" alt=""></mt-swipe-item>
    </mt-swipe>
    <!--<div>-->
    <!---->
    <!--</div>-->
    <div class="carMsg">
      <h4>{{carDetail.modelName}}</h4>
      <p>{{carDetail.price}}</p>
    </div>
    <div class="b-line"></div>
    <div class="plan">
      <div class="p-tit"></div>
      <div class="bb"></div>
      <div class="p-info">
        <div>
          <p></p>
          <p>{{carDetail.term}}</p>
        </div>
        <div>
          <p></p>
          <p>{{carDetail.firstPay}}</p>
        </div>
        <div>
          <p></p>
          <p>{{carDetail.monthRepay}}</p>
        </div>
      </div>
    </div>
    <div class="b-line"></div>
    <div class="youhui">
      <div class="yh-icon"></div>
      <div class="yh-txt"> </div>
    </div>
    <div class="b-line"></div>
    <mt-navbar v-model="selected">
      <mt-tab-item v-bind:id="1" ref='msg'></mt-tab-item>
      <mt-tab-item v-bind:id="2"></mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="selected">
      <mt-tab-container-item v-bind:id="1">
        <div class="default">
          <div class="d-tit"></div>
          <div class="bb"></div>
          <div class="d-info">
            <ul>
              <li><span></span><i>{{carDetail.structure}}</i></li>
              <li><span>//</span><i>{{carDetail.size}}</i></li>
              <li><span></span> <i>{{carDetail.engine}}</i></li>
              <li><span></span> <i>{{carDetail.gearbox}}</i></li>
              <li><span></span><i>{{carDetail.drive}}</i></li>
              <li><span></span><i>{{carDetail.fuel}}</i></li>
              <li><span></span><i>{{carDetail.consumption}}</i></li>
              <li><span></span><i>{{carDetail.color?carDetail.color:"-"}}</i></li>
            </ul>
          </div>
        </div>
        <div class="adv">
          <div class="a-tit"></div>
          <div class="a-info" v-for='(item,index) in carDetail.imgList'>
            <img :src="item"
              alt="">
            <p>{{carDetail.content[index]}}</p>
          </div>
          <!--<div class="a-info">-->
            <!--<img src="https://img.souche.com/9b38c53187733dfde1ae53b6bc5ec424.JPG?x-oss-process=image/resize,m_fill,w_1916,h_1277"-->
              <!--alt="">-->
            <!--<p>SUV,,</p>-->
          <!--</div>-->
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item v-bind:id="2">
        <div class="product-adv">
          <div class="p-tit"></div>
          <div class="bb"></div>
          <div class="pro-info">
            <div class='yi'>
               <br>
              
            </div>
            <div class='di'>
               <br>
              
            </div>
            <div class='kuai'>
               <br>
              
            </div>
            <div class='quan'>
               <br>
              
            </div>
          </div>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
    <div class="footer">
      <div class="call" @click='call()'>
        <span class='iconfont icon-dianhuazhengzaibohao'></span>
        <i></i>
      </div>
      <router-link class="appointment" v-bind="{to:'/login/'+id}">
        
      </router-link>
    </div>
  </div>
</template>

<script>
  import {MessageBox} from 'mint-ui';

  export default {
    name: "detail",
    data() {
      return {
        carDetail: "",
        open: false,
        docked: true,
        selected: 1,
        id: -1,
      }
    },
    created() {
     this.id =  this.$route.params.id.split("&")
      // this.id = this.$route.params.id
      //console.log(this.id[0]+'----'+this.id[1]);
      this.getCarInfo()
    },
    methods: {
      toggle(flag) {
        this.open = !this.open
        this.docked = !flag
      },
      getCarInfo() {
        let that = this
       // console.log(that.id);
        let url = this.$common.baseUrl + '/car/source/wx/getCarDetail';
        this.$axios.post(url + '?modelId=' + that.id[0] + '&carSourceId='+that.id[1]).then(function (res) {
          console.log(res);
          that.carDetail = res.data.data

         // console.log(that.carDetail.imgList);

        })
      },
      call() {
        // MessageBox({
        //   title: '400-680-8020',
        //   message: '  ',
        //   showCancelButton: true
        // });
        // MessageBox.confirm('?').then(action => {
          // let u = navigator.userAgent;
          // let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
          // let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
          // console.log("android" + isAndroid);
          // console.log("ios" + isiOS);
          window.location.href = "tel:400-680-8020";
        // });
      }
    }
  }
</script>

<style scoped lang='less'>
  @import "detail.less";

  .mint-swipe {
    height: 176px;
  }

  .mint-swipe-item {
    width: 100%;
    height: 176px;
    background-color: gray;
    text-align: center;
    line-height: 1.76rem;
    color: #ffffff;
  }

  .mint-swipe-indicator {
    width: 6px;
    height: 6px;
    background-color: #fff;
  }
</style>



